import React, { Component } from 'react';
import Page from 'components/LayoutComponents/Page';
import { BtnAddNew } from 'components/common/button';
import { CartHeadAction, TableData, ColEditDelete } from 'components/common';
import { Card, Row, Col, Spin, Button, Icon, Upload, message } from 'antd';
import { getSubjectEdu, deleteSubjectEdu } from 'services/subjectEdu';
import { uploadSubject } from 'services/Subject';
import { Link } from 'react-router-dom';
import routeConstant from 'routes/route/routeConstant';

const ROUTER_FORM_DATA = routeConstant.DANH_MUC__CHUONG_TRINH_DAO_TAO__MON_HOC__NHAP_DU_LIEU;

class ListSubjectEduPage extends Component {
  state = {
    reload: 0,
    loading: false,
  };
  handleDelete = async id => {
    await deleteSubjectEdu(id);
    message.success('Xa thnh cng');
    this.setState({ reload: this.state.reload + 1 });
  };

  handleUploadFile = async dataFile => {
    try {
      this.setState({
        loading: true,
      });
      await uploadSubject(dataFile.file);
      message.success('Import thnh cng');
      this.setState({
        reload: this.state.reload + 1,
      });
    } catch (error) {
      console.log(error);
    } finally {
      this.setState({
        loading: false,
      });
    }
  };

  render() {
    const { loading } = this.state;
    const columns = [
      {
        title: 'M mn',
        dataIndex: 'id',
        key: 'id',
      },
      {
        title: 'Tn mn',
        dataIndex: 'label',
        key: 'label',
      },
      {
        title: 'B mn',
        dataIndex: 'subjectGroup',
        key: 'subjectGroup',
      },
      {
        title: 'Khi kin thc',
        dataIndex: 'knowledge',
        key: 'knowledge',
      },
      {
        title: 'Loi mn',
        dataIndex: 'subjectType',
        key: 'subjectType',
      },
      {
        title: 'Thao tc',
        dataIndex: 'operation',
        render: (text, record) => (
          <ColEditDelete
            linkEdit={{
              to: {
                pathname: ROUTER_FORM_DATA,
                search: `?id=${record.id}`,
              },
            }}
            onConfirmDelete={() => this.handleDelete(record.id)}
          />
        ),
      },
    ];
    return (
      <Card>
        <Spin size="large" spinning={loading}>
          <CartHeadAction>
            <Link to={ROUTER_FORM_DATA}>
              <BtnAddNew />
            </Link>
            <Upload
              accept=".xlsx"
              beforeUpload={() => false}
              listType="text"
              onChange={uploadData => this.handleUploadFile(uploadData)}
              showUploadList={false}
            >
              <Button>
                <Icon type="upload" /> Chn file
              </Button>
            </Upload>
          </CartHeadAction>
          <Row>
            <Col span={24}>
              <TableData
                reload={this.state.reload}
                service={getSubjectEdu}
                columns={columns}
                rowKey="id"
              />
            </Col>
          </Row>
        </Spin>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <ListSubjectEduPage />
  </Page>
);



